@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.search-con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0000001c;
  border: 2px solid white;
  border-radius: 25px;
  padding: 0.3rem 0.8rem;
  width: 90%;
  max-width: 500px;
}

input.search-space {
  background: none;
  border: none;
  outline: none;
  color: white;
  width: 80%;
}
input.search-space:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 0.5; /* Firefox */
}
input.search-space::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 0.5; /* Firefox */
}

.song-result-card {
  padding: 10px;
  display: flex;
  align-items: center;
  width: 100%;
  color: white;
  border-bottom: rgba(255, 255, 255, 0.212) solid 2px;
}
.song-result-info-holder {
  margin-left: 15px;
  font-weight: 300;
}
.song-result-title {
  font-size: 24px;
}

.song-holder {
  max-height: 80%;
  overflow: auto;
  width: 100%;
}
.hide {
  display: none;
}

.search-page-center {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-page-con {
  width: 500px;
  width: 100%;

  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.253);
}
.music-icon {
  position: absolute;
  top: 0;
}

@media screen and (min-width: 550px) {
  .search-page-center {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .search-page-con {
    border-radius: 30px;
    width: 500px;
    border: 5px solid pink;
    height: 90vh;
  }
}

body {
  background-color: black;
}

.song-image-container img {
  width: 100%;
  height: auto;
}
.songCardHeaderMain {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 0;
}

.song-card-header {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 0.5rem;
}
.iconArrow {
  align-self: flex-start;
}

.song-title {
  color: white;
  font-weight: lighter;
}

.song-artist {
  color: white;
  font-weight: lighter;
}

.material-icons {
  color: white;
}

.song-details {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px;
}

.buttons-container {
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
  align-items: space-between;
}
.glow-on-hover {
  color: white;
  background-color: transparent;
  border: 2px solid #c28b87;
  margin: 5px;
  border-radius: 25px;
  padding: 7px 20px;
}

.audio-control {
  display: flex;
  justify-content: center;
  margin-top: 5px;
}
.audio {
  max-width: 320px;
  width: 80vw;
  border-radius: 25px;
  border: 2px solid #c28b87;
}

audio::-webkit-media-controls-panel {
  background-color: rgb(0, 0, 0);
}
audio::-webkit-media-controls-play-button {
  background-color: #c28b87;
  border-radius: 50%;
}
audio::-webkit-media-controls-mute-button {
  background-color: #c28b87;
  border-radius: 50%;
}
audio::-webkit-media-controls-current-time-display {
  color: white;
}
audio::-webkit-media-controls-time-remaining-display {
  color: white;
}
audio::-webkit-media-controls-timeline {
  background-color: #c28b87;
  border-radius: 25px;
  margin-left: 10px;
  margin-right: 10px;
}
audio::-webkit-media-controls-volume-slider {
  background-color: #c28b87;
  border-radius: 25px;
  padding-left: 8px;
  padding-right: 8px;
}
.allSong-button {
  padding: 5px;
  border-radius: 10px;
}

@media screen and (min-width: 550px) {
  .song-card-header {
    width: 80%;
    height: 660px;
    border: 5px solid #c28b87;
    border-radius: 30px;
  }
  .song-image-container img {
    border-radius: 24px;
  }
}

.album {
  padding-top: 40px;
  width: 100%;
  margin: auto;
  color: white;
  font-size: 36px;
  font-weight: 270;
  /* z-index: -10; */
}
.album-frame {
  width: 100%;
  height: 200px;
  border: 0px;
  /* z-index: -10; */
}

div#main-provider {
  z-index: -10;
}

@media screen and (min-width: 1000px) {
  .album {
    width: 50%;
  }
}

.lyrics-con {
  white-space: pre-line;
  display: flex;
  justify-content: center;
  color: white;
  height: 100%;
  background-color: rgb(0 0 0 / 83%);
  width: 100%;
}

.close-lyr {
  right: 0.8rem;
  top: 0.8rem;
  position: absolute;
  cursor: pointer;
}

.text-wrap {
  text-align: center;
  width: 100%;
}
.container {
  width: 100%;
  position: absolute;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  z-index: 10;
}
.text {
  padding: 2.2rem 0;
  background-color: rgb(0 0 0 / 83%);
}

@media screen and (min-width: 1000px) {
  .song-card {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}
body {
  margin: 0;
}

::-webkit-scrollbar {
  width: 8px;
}

/* ::-webkit-scrollbar-track {
  background: rgb(0, 0, 0);
  border-radius: 30px;
} */

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgb(112, 112, 112);
}

